import React from "react"
import { Button } from "reactstrap"
import DataTable from "react-data-table-component"
import TableCustomHeader from "../../../MainDataTable/TableCustomHeader";
import { Edit, Trash2 } from "react-feather"
import Can from '../../../../../utility/permissions/can';

class LiquorRegistrationTable extends React.Component {

    tableProperties = {
        columns: [
            {
                name: "Action",
                cell: (row) => (
                    <>
                        <Can I="update" a={this.props.role} passThrough>
                            {allowed =>
                                <Button.Ripple className="mr-1 mb-1 bg-gradient-success" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleEdit(row)}
                                >
                                    <Edit size={14} />
                                </Button.Ripple>
                            }
                        </Can>

                        <Can I="delete" a={this.props.role} passThrough>
                            {allowed =>
                                <Button.Ripple className="mr-1 mb-1  bg-gradient-danger" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleDelete(row)}
                                ><Trash2 size={14} />
                                </Button.Ripple>
                            }
                        </Can>
                    </>
                )
            },
            {
                name: "Liquor Number",
                selector: "REG_LIQUOR_NUMBER",
                sortable: true,
                cell: row => <p className="mb-0">{row.REG_LIQUOR_NUMBER}</p>
            },
            {
                name: "Business Name",
                selector: "BUSINESS_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.BUSINESS.BUSINESS_NAME}</p>
            },
            {
                name: "SubCounty",
                selector: "SUBCOUNTY_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.SUBCOUNTY.SUBCOUNTY_NAME}</p>
            },
            {
                name: "Ward",
                selector: "WARD_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.WARD.WARD_NAME}</p>
            },
            {
                name: "Revenue Type",
                selector: "FEECATEGORY_CODE",
                sortable: true,
                cell: row => <p className="mb-0">{row.REVENUECATEGORY.FEECATEGORY_CODE}</p>
            },
            {
                name: "Revenue Name",
                selector: "FEE_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.REVENUE.FEE_NAME}</p>
            },
            {
                name: "Status",
                selector: "SYS_STATUS_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.SYS_STATUS.SYS_STATUS_NAME}</p>
            },
            {
                name: "Client Name",
                selector: "CLIENT_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.CLIENT.CLIENT_NAME}</p>
            },
            {
                name: "ModuleType",
                selector: "MODULE_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.MODULE.MODULE_NAME}</p>
            }
        ]
    }

    render() {

        return (
            <>
                <DataTable
                    className="dataTable-custom"
                    data={this.props.value.length ? this.props.filteredData : this.props.data}
                    columns={this.tableProperties.columns}
                    noHeader
                    pagination
                    subHeader
                    striped
                    responsive
                    subHeaderComponent={
                        <TableCustomHeader role={this.props.role} value={this.props.value} handleShow={this.props.handleShow} handleFilter={this.props.handleFilter} />

                    }
                />
            </>

        )
    }
}


export default LiquorRegistrationTable