    populateData() {
        const getdata = `
        query{
            LiquorRegisters{
                    REG_LIQUOR_ID
                    REG_LIQUOR_NUMBER
                    REG_BUSINESS_ID
                    BUSINESS{
                       BUSINESS_ID
                       BUSINESS_NAME
                    }
                    SET_SUBCOUNTY_ID
                    SUBCOUNTY{
                        SUBCOUNTY_ID
                        SUBCOUNTY_NAME
                    }
                    SET_WARD_ID
                    WARD{
                      WARD_ID
                      WARD_NAME
                    }
                    SET_REVENUE_CATEGORY_ID
                    REVENUECATEGORY{
                         FEECATEGORY_ID
                         FEECATEGORY_NAME
                         FEECATEGORY_CODE
                    }
                    SET_REVENUE_FEE_ID
                    REVENUE{
                         FEE_ID
                         FEE_NAME
                    }
                    SYS_STATUS_ID
                    SYS_STATUS{
                          SYS_STATUS_ID
                          SYS_STATUS_NAME  
                    }
                    CLIENT_ID
                    CLIENT{
                       CLIENT_ID
                       CLIENT_NAME
                       CLIENT_DOCUMENT_NUMBER
                       POSTAL_ADDRESS
                       MOBILE_NUMBER
                    }
                    SET_MODULE_ID
                    MODULE{
                        MODULE_ID
                        MODULE_NAME
                    }
            }
          }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.LiquorRegisters
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

