    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.REG_LIQUOR_NUMBER.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SYS_STATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SUBCOUNTY.SUBCOUNTY_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.WARD.WARD_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.REVENUECATEGORY.FEECATEGORY_CODE.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.REVENUE.FEE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.BUSINESS.BUSINESS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.CLIENT.CLIENT_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.MODULE.MODULE_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.REG_LIQUOR_NUMBER.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SYS_STATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SUBCOUNTY.SUBCOUNTY_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.WARD.WARD_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.REVENUECATEGORY.FEECATEGORY_CODE.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.REVENUE.FEE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.BUSINESS.BUSINESS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.CLIENT.CLIENT_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.MODULE.MODULE_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

